import { Route, Redirect, Switch } from 'react-router'
import Loadable from 'react-loadable'
import React from 'react'

import { ViewerContext } from '../Context/ViewerProvider'
import FullScreenLoader from '../Components/FullScreenLoader'
import MenuBar from '../Components/MenuBar'
import PRoute from '../Components/PrivateRoute'

export default () => (
  <ViewerContext.Consumer>
    {({ loading, viewer }) => {
      if (loading) return <FullScreenLoader />
      if (!viewer) {
        return (
          <Switch>
            <Route
              exact
              path='/welcome'
              name='home'
              component={Loadable({
                loader: () => import('../Pages/HomePage'),
                loading: FullScreenLoader
              })}
            />
            <Redirect from='*' to='/welcome' />
          </Switch>
        )
      }
      return (
        <React.Fragment>
          <MenuBar />
          <Switch>
            <PRoute
              exact
              path='/items'
              name='items'
              component={Loadable({
                loader: () => import('../Pages/ItemsPage'),
                loading: FullScreenLoader
              })}
            />
            <PRoute
              exact
              path='/profile'
              name='profile'
              component={Loadable({
                loader: () => import('../Pages/ProfilePage'),
                loading: FullScreenLoader
              })}
            />
            <PRoute
              exact
              path='/profile/:userId'
              name='profile'
              component={Loadable({
                loader: () => import('../Pages/ProfilePage'),
                loading: FullScreenLoader
              })}
            />
            <PRoute
              exact
              path='/share'
              name='share'
              component={Loadable({
                loader: () => import('../Pages/SharePage'),
                loading: FullScreenLoader
              })}
            />
            <Redirect from='*' to='/items' />
          </Switch>
        </React.Fragment>
      )
    }}
  </ViewerContext.Consumer>
)



