// ACTIONS
export const UPDATE_NEW_ITEM = 'UPDATE_NEW_ITEM'
export const RESET_NEW_ITEM = 'RESET_NEW_ITEM'
export const RESET_NEW_ITEM_IMAGE = 'RESET_NEW_ITEM_IMAGE'

// ACTION CREATORS
export const updateNewItem = item => ({ type: UPDATE_NEW_ITEM, payload: item })
export const resetNewItem = item => ({ type: RESET_NEW_ITEM })
export const resetNewItemImage = item => ({ type: RESET_NEW_ITEM_IMAGE })



